import Link from "next/link";
import Image from "next/image";
import logo from '../../public/graphics/756-Logo.png';
import insta from "../../public/graphics/instagram.png";

export default function Header() {
    return (
    <header className="bg-black text-white">
        <div className="flex justify-between items-center p-4">
            <div>
                <Link href="/">
                    <Image src={logo} alt="Logo" width={50} height={50} />
                </Link>
            </div>
            <div>
                <Link href="https://www.instagram.com/">
                    <Image src={insta} alt="Instagram" width={30} height={30} />
                </Link>
            </div>
        </div>
        <nav className="flex justify-between items-center p-4">
            <div>
            <Link href="/" className="text-white">
                Home
            </Link>
            </div>
            <div>
            <Link href="/about" className="text-white">
                About
            </Link>
            </div>
            <div>
            <Link href="/services" className="text-white">
                Services
            </Link>
            </div>
            <div>
            <Link href="/contact" className="text-white">
                Contact
            </Link>
            </div>
        </nav>
    </header>
    );
} 
 //The  Header  component is a simple navigation bar that contains links to the Home, About, Services, and Contact pages. 
 //The  Link  component from Next.js is used to create links to the different pages. 
 //The  Header  component is then imported into the  Layout  component.